.App {
  text-align: center;
}

h1.question{
  font-size: 22px;
}

button.btnAnswer{
  display: block;
  margin: 0 auto;
  width: 200px;
  background-color: green;
  font-size: 15px;
  cursor: pointer;
  color: white;
  margin-bottom: 10px;
  padding: 10px 10px;
  border-radius: 1px;
}

button.btnAnswer:hover{
  opacity:0.8;
}

.scoreWrapper{
  position: absolute;
  bottom: 0px;
  width: 100%;
  background-color: #f5f5f5;
  text-align: center;
}

h3.correctAns{
  display: inline-block;
  vertical-align: middle;
  margin: 1em;
}

h3.incorrectAns{
  display: inline-block;
  vertical-align: middle;
  margin: 1em;
}

.userGreeting{
  font-size: 14px;
  text-align: center;
}